
.color {
  display: inline-block;
  cursor: pointer;
  border: 3px solid transparent;
  padding: 3px;
  width: 52px;
  height: 52px;
}
.color span {
  display: inline-block;
  width: 40px;
  height: 40px;
}
.selected {
  border: 3px solid #67BE4B;
}



